import { css } from "@emotion/react";

export let SanSerifFont = "Messina";
export let SerifFont = "Domaine";
export let BoldFont = "Twm";
export let ItalicFont = "HowltItalic";
export const setSanSerifFont = (font) => {
	SanSerifFont = font;
};
export const setBoldFont = (font) => {
	BoldFont = font;
};

export const setSerifFont = (font) => {
	SerifFont = font;
};

export const setItalicFont = (font) => {
	ItalicFont = font;
};
export const SanSerif = (props) =>
	css`
		font-family: ${SanSerifFont}, "", "Yu Gothic", YuGothic,
			Hiragino Sans, "", Hiragino Kaku Gothic ProN,
			" ProN W1", "Hiragino Kaku Gothic ProN W1",
			"Hiragino Kaku Gothic Pro", "", Meiryo, Osaka, sans-serif;
	`;
export const Italic = (props) =>
	css`
		font-family: ${ItalicFont}, ${SerifFont}, "", YuMincho,
			" ProN W3", "Hiragino Mincho ProN", "HGE", " ",
			" ", serif;
		font-style: italic;
	`;
export const Bold = (props) =>
	css`
		font-family: ${BoldFont}, ${SanSerifFont}, "", "Yu Gothic",
			YuGothic, Hiragino Sans, "", Hiragino Kaku Gothic ProN,
			" ProN W1", "Hiragino Kaku Gothic ProN W1",
			"Hiragino Kaku Gothic Pro", "", Meiryo, Osaka, sans-serif;
	`;
export const Serif = (props) =>
	css`
		font-family: ${SerifFont}, "", YuMincho, " ProN W3",
			"Hiragino Mincho ProN", "HGE", " ", " ", serif;
	`;
